<template>
  <div class="submitfrom">
    <div v-if="!submitted">

      <div class="form-group">
        <label for="id">Id</label>
        <input type="text" class="form-control" id="id" required v-model="task.id" name="id">
      </div>

      <div class="form-group">
        <label for="desc">Description</label>
        <textarea type="text" class="form-control" id="desc" required v-model="task.description" name="desc"></textarea>
      </div>

      <div class="form-group">
        <label for="date">Date</label>
        <input type="date" class="form-control" id="date" required v-model="task.date" name="date">
      </div>

      <div class="form-group">
        <label for="pr">Priority</label>
        <b-form-select type="text" class="form-control" id="pr" required v-model="task.priority" name="pr" :options="priorityes"/>
      </div>

      <button @click="saveTask()" class="btn btn-success">Submit</button>

    </div>
    <div v-else>
      <h4>You submited sucessfully</h4>
      <button class="btn btn-succes" @click="newTask()">Add</button>
    </div>
  </div>
</template>
<script>
import http from '../http-common';

export default {
  name: 'add-task',
  data () {
    return {
      task: {
        id: '',
        description: '',
        date: '',
        priority: ''
      },
      submitted: false,
      priorityes: ['Alta','Media','Baja']
    }
  },
  methods: {
    saveTask () {
      var data = JSON.stringify(this.task)
        
      console.log(data);

      http
        .post('/tasks', data)
        .then(res => {
          this.task.id = res.data.id;
          console.log(res.data);
        })
        .catch(err => {
          console.log(err);
        })
      this.submitted = true;
    },
    newTask () {
      this.submitted = false;
      this.task = {}
    }
  }
}
</script>

<style>
.submitfrom {
  max-width: 300px;
  margin: auto;
}
</style>
